import React, { Component } from 'react'
import { TextField, Button } from '@material-ui/core';
import { createHash } from 'crypto';

export default class PasswordProtection extends Component {
    constructor(props) {
        super(props);
        this.state = {
            passwordValue: '',
            error: false
        };
    }

    handleChange = e => {
        this.setState({ 
            passwordValue: e.target.value,
            error: false
        });
    }

    handleClick = () => {
        const targetHash = 'de0c9a74625fb3d77cca5140159886a8bd67d1fd3c435e8933c1a25ba24455a65dbf4045686552d7eae37b83dbc48ab663a81d9ef640176a7187ea636afa78bb';
        const hashedPassword = createHash('sha512').update(this.state.passwordValue).digest('hex');
        if (hashedPassword === targetHash) {
            this.props.onSuccess();
        }
        else {
            this.setState({ error: true });
        }
    }

    render() {
        return (
            <div>
                <div>
                    <TextField
                        label="Password"
                        variant="outlined"
                        type="password"
                        error={this.state.error}
                        value={this.state.passwordValue}
                        onChange={this.handleChange}
                    />
                </div>
                <div style={{ marginTop: '15px' }}>
                    <Button
                        variant="contained"
                        color="primary"
                        type="submit"
                        onClick={this.handleClick}
                    >
                        Entrar
                    </Button>
                </div>
            </div >
        );
    }
}
